@import "variables";
@import "../../mixins";

@mixin u-tab-theme {

  @each $color, $value in $u-theme-colors {
    @include apply-style-bg-once("tab-bar-#{$color}") {
      .u-tab-bar-#{$color} {
        @include u-tab-bar-variant(var(--u-#{$color}-color));
      }
    }
  }
}

@mixin u-tab-bar-variant($color) {
  --u-tab-bar-color: #{$color};
  @extend %u-tab-bar-colored;
}
